.page-heading,
.page-contents {
  display: grid; }

.page-contents {
  grid-template-columns: minmax(360px, 900px);
  grid-template-areas: "content-1" "content-2";
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

.content-1 {
  grid-area: content-1; }

.content-2 {
  grid-area: content-2; }

div[class*="content-"] {
  display: flex;
  flex-direction: column; }

div[class*="content-"] > div {
  min-height: 4em;
  justify-content: left;
  margin: 1px 0;
  background: rgba(128, 128, 128, 0.65);
  width: 100%; }

.caption-icon {
  font-size: 2.5rem;
  display: inline-flex; }

.detail-list {
  display: flex;
  flex-direction: column; }

.detail-field {
  align-items: center;
  padding-left: 0; }

.detail-field .field-label {
  width: 140px; }

.detail-field .field-value {
  flex: 1;
  max-width: 388px; }

.full-width {
  max-width: 100% !important; }

.pimg-card {
  width: 200px;
  height: 120px;
  background-size: cover;
  background-position: center; }

.img-uploader {
  width: 388px; }

.group-list .q-item {
  padding: 4px 0;
  display: flex; }

.group-list > .q-item:first-child .q-input {
  max-width: 180px; }

.detail-field-keyval {
  display: flex;
  flex-wrap: wrap; }

.detail-list-key,
.detail-list-value {
  margin: 2px 2px;
  flex: 1 1 164px; }

.option-list-key {
  margin: 2px 2px;
  flex: 1 1 228px; }

.option-list-price {
  margin: 2px 2px;
  flex: 1 1 100px; }

.remove-icon {
  margin-right: -16px; }

.qtext-editor {
  border: 2px solid white; }

.has-error {
  border: 2px solid #c9124f; }

@media (max-width: 500px) {
  .detail-field {
    flex-direction: column;
    align-items: flex-start; }
  .detail-field .field-label,
  .detail-field .field-value {
    line-height: 2em;
    width: 100%; } }
